import React, { useEffect, useState } from 'react';
import {Link} from 'gatsby'
import { window, document, exists } from 'browser-monads';

const Nav = () => {
    const current = window.location.pathname
    /*useEffect(() => {
      const current = window.location.pathname
    }, [])*/
    return (
        <nav>
            <Link to="/" style={{color: current === '/' ? '#fff' : '#000'}}> home </Link>
            <Link to="/atelier" style={{color: current === '/atelier' ? '#fff' : '#000'}}> ateliers </Link>
            <Link to="/philosophie" style={{color: current === '/philosophie' ? '#fff' : '#000'}}> philosophie </Link>
            <Link to="/consulting" style={{color: current === '/consulting' ? '#fff' : '#000'}}> consulting </Link>
            <Link to="/equipe" style={{color: current === '/equipe' ? '#fff' : '#000'}}> équipe </Link>
            <Link to="/contact" style={{color: current === '/contact' ? '#fff' : '#000'}}> contact </Link>
        </nav>
    );
};

export default Nav;