import React from 'react';
import { useStaticQuery, graphql } from "gatsby"

const Ruban = () => {

    const data = useStaticQuery(graphql`
    query RubanQuery {
        allStrapiRuban {
          edges {
            node {
              texte
              strapiId
              id
            }
          }
        }
   }
  `)

    console.log(data);
    return (
            <p class="marquee"><span>{data.allStrapiRuban.edges[0].node.texte}</span></p>
    );
};

export default Ruban;

