import React, { useEffect } from 'react';
import Layout from '../components/layout';
import AtelierCard from '../components/atelier/atelier-card';


const Subscription = props => {
    if (props.location.state.atelier.titre) {
        return (
            <Layout>
                <AtelierCard
                    atelier={props.location.state.atelier}
                />
                <form>k
                    <div>
                        <label>Nom</label>
                        <input type="text" />
                    </div>
                    <div>
                        <label>Prenom</label>
                        <input type="text" />
                    </div>
                    <div>
                        <label>Entreprise</label>
                        <input type="text" />
                    </div>
                    <div>
                        <label>Adresse</label>
                        <input type="number" />
                    </div>
                    <div>
                        <label>NPA</label>
                        <textarea />
                    </div>
                    <div>
                        <label>Localité</label>
                        <input type="number" />
                    </div>
                    <div>
                        <label>Email</label>
                        <input type="number" />
                    </div>
                    <div>
                        <label>Nombre de participant</label>
                        <input type="number" />
                    </div>
                </form>
            </Layout>

        );
    } else {
        window.location.pathname = '/'
        return null
    }
};

export default Subscription;